import React, { Component } from 'react';
import '../App.css';
import SimpleImageSlider from "react-simple-image-slider";

import ReactAvatar from 'react-avatar';
import { 
  BrowserRouter as Router, NavLink, Switch,
  Route,
  Link, 
} from 'react-router-dom'; 
import Box from '@material-ui/core/Box';

var nameTeacher = "Mr. Prithviwasi Rajesh";
var name = "Navodya College Of Arts";
var classes = "5/week";
var fees = "Null";
var experience = "3+years";
var type = 'Painting, Sketching';
var age = "10th and above";
var duration = "1-4hrs/day";
var timings = "4-7pm"
var bigImage = "https://firebasestorage.googleapis.com/v0/b/pidgin-ds.appspot.com/o/artClasses%2FnavodyaCollegeOfArt%2FNavodyaCollegeOfArt_bigImage.jpeg?alt=media&token=b81d505d-9fe6-4e48-aa05-3ed13971c513";
var profileImage = "https://firebasestorage.googleapis.com/v0/b/pidgin-ds.appspot.com/o/artClasses%2FnavodyaCollegeOfArt%2FNavodyaCollegeOfArt_image.jpeg?alt=media&token=dca6ca4a-d2a0-4784-a9c4-59be1d778b7a";
var colorOverlay = "rgba(242,128,149,0.5)";

const slideImages = [
  "https://firebasestorage.googleapis.com/v0/b/pidgin-ds.appspot.com/o/artClasses%2FnavodyaCollegeOfArt%2FE402FA6C-4950-4763-B1F2-60957FC519EA%20-%20NAVODAYA%20COLLAGE%20OF%20ART.jpeg?alt=media&token=139d1d99-502a-4363-94af-ebd09ad10d37",
  "https://firebasestorage.googleapis.com/v0/b/pidgin-ds.appspot.com/o/artClasses%2FnavodyaCollegeOfArt%2F4B07E135-9EB5-4633-B640-164C49B2ACEA%20-%20NAVODAYA%20COLLAGE%20OF%20ART.jpeg?alt=media&token=134f43df-4b45-488a-86ce-e5e998433ce5",
  "https://firebasestorage.googleapis.com/v0/b/pidgin-ds.appspot.com/o/artClasses%2FnavodyaCollegeOfArt%2FC1B241CE-E574-445B-A287-2C430E31630D%20-%20NAVODAYA%20COLLAGE%20OF%20ART.jpeg?alt=media&token=ffd12b60-c95d-4f2c-bb1c-783d2a436abf" ,
];

const images = [
  { url: "https://firebasestorage.googleapis.com/v0/b/pidgin-ds.appspot.com/o/artClasses%2FnavodyaCollegeOfArt%2FE402FA6C-4950-4763-B1F2-60957FC519EA%20-%20NAVODAYA%20COLLAGE%20OF%20ART.jpeg?alt=media&token=139d1d99-502a-4363-94af-ebd09ad10d37" },
  { url: "https://firebasestorage.googleapis.com/v0/b/pidgin-ds.appspot.com/o/artClasses%2FnavodyaCollegeOfArt%2F4B07E135-9EB5-4633-B640-164C49B2ACEA%20-%20NAVODAYA%20COLLAGE%20OF%20ART.jpeg?alt=media&token=134f43df-4b45-488a-86ce-e5e998433ce5" },
];

export default function NavodyaCollegeOfArt(){

  
    return (
      <Router>
      <div class="App">
        <div class="wallpaper">
          <img src={bigImage} alt="Avatar" class="image"/>
          <div class="overlay" style={{ background: colorOverlay}}>
            <div class="avatar">
            <ReactAvatar src={profileImage} round="50%" size="150" style={{background:"white"}}/>
            </div>
          </div>
        </div>
        <div class="details">
        <Box boxShadow={2} bgcolor="transparent">
            <table class="table">
              <tr class="tr">
                <td class="th">{classes}</td>
                <td class="th">{fees}</td>
                <td class="th">{experience}</td>
              </tr>
              <tr>
                <td align="center">Classes</td>
                <td align="center">Fees</td>
                <td align="center">Experience</td>
              </tr>
            </table>
        </Box>
        </div>
  
        <div class="container">
            <div class="head">General</div>
            <div class="info">
              <div class="field">Age</div>
              <div class="attribute">{age}</div>
            </div>
            <div class="info">
              <div class="field">Type</div>
              <div class="attribute">{type}</div>
            </div>
            <div class="info">
              <div class="field">Duration</div>
              <div class="attribute">{duration}</div>
            </div>
            <div class="info">
              <div class="field">Timings</div>
              <div class="attribute">{timings}</div>
            </div>
            <div class="head">Wall of Fame</div>

            
            <div class="carousel">
              <img src={slideImages[0]}/>         
              <img src={slideImages[1]}/>
              <img src={slideImages[2]}/>
            </div>

            <div class="head">Eligibility</div>
              <ul class="list">
                <li>For diploma: Student should be 12th pass.</li>
                <li>For any other course: No criteria.</li>
              </ul>
            <div class="head">Qualifications</div>

            <div class="listHeader">{nameTeacher}</div>
            <ul class="list">  
                <li>Degree and Diploma in Arts</li>
              </ul>

            <div class="head">Vision and Mission</div>
            <div class="listHeader">Dear Students,</div>
            <ul class="list">  
                <li>{name} provides you with practical and theoritical knowledge, with market stratergies. </li>
                <li>Here you will gain knowledge on many designing softwares.</li>
                <li>Main mission of {name} is to represent the importance of fine art and help with your career in this field.</li>
              </ul>
            <div style={{height:"60px"}}></div>
          </div>

          <Switch>
          <Route exact path="/showImage">
            <ShowImages></ShowImages>
          </Route>
        </Switch>
  
      </div>
      </Router>
    );
  
}

function ShowImages(){
  return(
        <div style={{height:"100%",width:"100%"}}>
            <SimpleImageSlider
                width={896}
                height={504}
                images={images}
            />
      </div>
  );
}

